#container.community {
  width: 80%;
  margin: 0 auto;
  padding: 20px;
  padding-top: 70px;
  box-sizing: border-box;
}

.content-area {
  display: flex;
  justify-content: space-between;
}

/* Left side */
.leftside {
  flex-basis: 10%;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.leftside .card {
  border: 1px solid #d6d6d6;
  background-color: #f9f9f9;
  padding: 15px;
}

.logged {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.picture {
  width: 60px;
  height: 60px;
  border-radius: 80%;
  margin-bottom: 10px;
}

.nickname {
  font-size: 16px;
  font-weight: bold;
  color: #333;
  margin-bottom: 10px;
}

.menus {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.menus a {
  text-decoration: none;
  color: #000;
  font-size: 14px;
  border-bottom: 1px solid #e3e3e3;
}

.menus a:hover {
  background-color: #f0f0f0;
}

/* Middle section */
.main {
  flex-basis: 60%;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.card-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  row-gap: 10px;
}

.card-container .card {
  height: 300px;
  border: 1px solid #d6d6d6;
  background-color: #f9f9f9;
  width: calc(50% - 10px);
  padding: 5px;
}

/* Right side */
.rightside {
  width: 20%;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.rightside .card {
  border: 1px solid #d6d6d6;
  background-color: #f9f9f9;
  padding: 15px;
}

.board .article,
.board .list {
  display: block;
  padding: 10px;
  border-top: 1px solid #e3e3e3;
  color: #444;
}

.board .article p.title {
  font-weight: bold;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.board .article p.small,
.board .list p {
  line-height: 16px;
  color: #737373;
  font-size: 12px;
}

/* 中间区域的大方块样式 */
.post-container {
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
}

/* 帖子列表样式 */
.post-list table {
  width: 100%;
  border-collapse: collapse;
}

.post-list th, .post-list td {
  padding: 8px 12px;
  border: 1px solid #ddd;
}

.post-list th {
  background-color: #f5f5f5;
  font-weight: bold;
}

.post-link {
  background: none;
  border: none;
  color: #007bff;
  cursor: pointer;
  text-decoration: underline;
}

.post-link:hover {
  color: #0056b3;
}

/* 帖子详情样式 */
.post-detail {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.post-detail h2 {
  margin-bottom: 16px;
}

.post-detail .content {
  margin-top: 20px;
  white-space: pre-wrap;
}

.back-button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.back-button:hover {
  background-color: #0056b3;
}

